  --primary-text: #fcfcfc;
  --secondary-text: #131313;
  --accent: #f8d24e;
  --primary-bg: #84a178;
  --secondary-bg: #fcfcfc;
  --additional-bg: #fff;
  --first-prod-bg-: #aec6a5;
  --second-prod-bg: #efd478;
  --third-prod-bg: #d6936d;
  --btn-bg: #f8d24e;
  --btn-bg-hover: #0a8817;
  --btn-bg-disabled: #ababab;
  --dividing-line: #e1e1e1;
  --input-bg: rgba(248, 210, 78, .1);
  --first-prod-gr: (0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7)), #d9d9d9;
  --second-prod-gr: (0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7)), #d9d9d9;
  --third-prod-gr: (0deg, rgba(213, 117, 63, .7), rgba(213, 117, 63, .7)), #d9d9d9;
  --prods-circles-shadow: 0px 5.26027px 19.726px rgba(153, 114, 23, .25);
  --prod-cont-cards-shadow: 0px 8px 30px rgba(212, 20, 67, .1);
  --stats-shadow: 0px 6px 20px rgba(10, 136, 23, .25);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
